
 $primary_color : #3f7af8;

 html, body {
  margin: 0;
  padding:0;
    font-family: 'Inter', sans-serif;
}

@media only screen and (max-width: 767px) {
  /* STYLES HERE for BROWSER WINDOWS with a max-width of 480px. 
     This will work on desktops when the window is narrowed.  */
  .banner-adv .post-image {
    width: 100%;
    height: auto;
  }
  .side-banner {
    display: none;
  }
  .header .navbar-main li {
    list-style: none;
    padding: 12px;
  }
  .navbar-main li a {
    font-size: 12px;
  }
}

a {
  text-decoration: none !important;
}


/* .MuiContainer-maxWidthLg {
    background: #fff;
  } */

  
  
.footer {
  background: #1a1919ef;
}

/* starts */

.header{
  background-color: #5f60c3;
  box-shadow: 0px 0px 19px -7px #b5b5b5;


.navbar-main {
  display: flex;
  flex-direction: row;
  margin: 0;
  align-items: center;
}

.navbar-main li {
  list-style: none;
  a{
      display: block;
      padding: 4px 19px;
      color: $primary_color;
      margin-left: 15px;
      border-radius: 4px;
      &.react{
        background-color: #20232a;
        color: #61dafb;
        font-weight: 400;
      }
      &.javascript{
        background-color: yellow;
        color: black;
        font-weight: 400;
      }
  }
  
}

.navbar-main a {
  text-decoration: none;
}

.header-content {
  padding: 18px 0;

  .logo-wrp{
    a{
      height: 100%;
      display: block;
      padding: 4px 19px;
      color: $primary_color;
      background-color: #fff;
      border-radius: 4px;
      font-weight: 400;

    }
  }
}
}




.advertisement {
  margin-bottom: 50px;
  text-align: center;
  vertical-align: middle;
  position: relative;
}

.advertisement span {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  color: rgb(90, 90, 90);
}

.RichEditor-root {
  background: #fff;
  border: 1px solid #ddd;
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  padding: 15px;
}

.RichEditor-editor {
  border-top: 1px solid #ddd;
  cursor: text;
  font-size: 16px;
  margin-top: 10px;
}

.RichEditor-editor .public-DraftEditorPlaceholder-root,
.RichEditor-editor .public-DraftEditor-content {
  margin: 0 -15px -15px;
  padding: 15px;
}

.RichEditor-editor .public-DraftEditor-content {
  min-height: 100px;
}

.RichEditor-hidePlaceholder .public-DraftEditorPlaceholder-root {
  display: none;
}

.RichEditor-editor .RichEditor-blockquote {
  border-left: 5px solid #eee;
  color: #666;
  font-family: 'Inter', sans-serif;
  font-style: italic;
  margin: 16px 0;
  padding: 10px 20px;
}

.RichEditor-editor .public-DraftStyleDefault-pre {
  background-color: #1f2937;
  font-family: 'Inter', sans-serif;
  font-size: 16px;
  padding: 20px;
  border-radius: 10px;
}
pre{
  color: white;
}

.RichEditor-controls {
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  margin-bottom: 5px;
  user-select: none;
}

.RichEditor-styleButton {
  color: #999;
  cursor: pointer;
  margin-right: 16px;
  padding: 2px 0;
  display: inline-block;
}

.RichEditor-activeButton {
  color: #5890ff;
}

.posts .post {
  margin: 30px 0;
}

.post.RichEditor-editor {
  border-top: none;
  padding-bottom: 10px;
}

.post-wrap {
  border-bottom: 1px solid #ddd;
  padding-bottom: 20px;
}
.post-wrap a {
  display: flex;
}

.post-wrap a .post-image {
  margin-top: 30px;
  width: 20%;
}
.post-wrap a .post-image img {
  width: 100%;
  height: auto;
}
.post-wrap a .post-preview {
  width: 80%;
}

.post .title {
  color: #0f1e26;
  line-height: 1.3;
  font-size: 35px;
  padding-bottom: 20px;
  text-align: center;
  border-bottom: 1px solid #e5e7eb;
}

.post .post-content{
  font-size: 20px;
}

#authorBox {
  background: #f0f0f0;
  padding: 20px;
  overflow: hidden;
  color: #333;
  width: 216px;
}

#authorBox h3 {
  font-size: 18px;
  color: #333;
  margin: 0;
  padding: 10px 10px 5px 10px;
}

#authorBox h4 {
  margin: 0;
  margin-bottom: 10px;
}

#authorBox h3 a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

#authorBox img {
  margin: 5px 10px 0 5px;
  padding: 5px;
  float: left;
  border: 1px solid #ddd;
  max-width: 50px;
  height: auto;
}

#authorBox p {
  color: #333;
  margin: 0;
}

#authorBox p a {
  color: #333;
}

.author-wrap #authorBox {
  float: right;
}

.logo {
  height: 35px;
}

.edittor-wrp {
  margin-bottom: 20px;
}
.author-id-form {
  margin-bottom: 10px;
}

.author-id-form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.author-id-form .grp {
  margin-bottom: 10px;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.clearfix {
  display: inline-block;
}

/* start commented backslash hack \*/

* html .clearfix {
  height: 1%;
}

.clearfix {
  display: block;
}

/* close commented backslash hack */




.banner-adv {
  padding: 25px 0;
  text-align: center;
  margin-bottom: 24px;
}

h4 {
  margin: 12px 0;
}

.test-svg {
  position: absolute;
  bottom: -35px;
  left: 55px;
  width: 505px;
  height: 12px;
}

.victory-chart {
  height: 300px;
  width: 600px;
  position: relative;
}

.chart-area {
  margin-bottom: 100px;
}

.bttn-wrap {
  display: flex;
  justify-content: flex-end;
}

.share-icon-container button{
    margin-left: 10px;
}